<template>
  <div class="modal">
    <div v-if="winner" class="content">
      <img :src="winnerPic" />
      <p class="head head-win">You've won!</p>
      <div class="results">
        <p>+{{winningRes.expGained}} exp</p>
        <p class="middle">+{{winningRes.coins}} coins</p>
        <p>+1 streak</p>
      </div>
      <button @click="$router.push({ name: 'home' })" class="btn btn-win">
        Continue
      </button>
    </div>

    <div v-else class="content">
      <img :src="loserPic" />
      <p class="head">You've lost...</p>
      <div class="results">
        <p>-{{losingRes.expLost}} exp</p>
        <p class="middle">+{{losingRes.coins}} coins</p>
        <p>-{{1}} streak</p>
      </div>

      <button @click="$router.push({ name: 'home' })" class="btn">
        Continue
      </button>
    </div>
  </div>
</template>

<script>
export default {
  props: ["data"],
  data() {
    return {
      loserPic: require("@/assets/img/loser.jpg"),
      winnerPic: require("@/assets/img/winner.jpg"),
    };
  },
  computed: {
    myId() {
      return this.$store.getters["user/getUserData"].id;
    },
    winner() {
      return this.data.winner.playerData.id === this.myId;
    },
    winningRes(){
      return this.data.winner.results
    },
    losingRes(){
      return this.data.loser.results
    }
  },
  created() {
    if (this.winner) {
      this.$store.dispatch("audio/playWin");
      this.$store.commit("user/SET_USER", this.data.winner.playerData);
    } else {
      this.$store.dispatch("audio/playLose");
      this.$store.commit("user/SET_USER", this.data.loser.playerData);
    }
  },
};
</script>

